.eboard-disable .eboard-tabs {
  visibility: hidden;
}
.eboard-tabs {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  height: 32px;
  z-index: 10;
  white-space: nowrap;
  /*     ~ .eboard-body{
            bottom: @height;
        }*/
}
.eboard-tab {
  width: 140px;
  height: 32px;
  display: inline-block;
  vertical-align: middle;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background: #a1bed1;
  text-align: center;
  line-height: 20px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  padding: 6px;
  box-sizing: border-box;
  color: white;
  cursor: pointer;
  position: relative;
}
.eboard-tab:first-of-type .eboard-tab-remove {
  display: none;
}
.eboard-tab.eboard-tab-active {
  background: #4c84ff;
}
.eboard-tab ~ .eboard-tab {
  margin-left: 5px;
}
.eboard-tab-remove {
  font-size: 20px;
  position: absolute;
  line-height: 20px;
  right: 3px;
  top: 50%;
  transform: translateY(-50%);
  color: #ec272a;
  opacity: 0;
  cursor: pointer;
}
.eboard-tab-remove:hover {
  opacity: 1!important;
}
.eboard-tab:hover .eboard-tab-remove {
  opacity: 0.6;
}
.eboard-tab-add {
  height: 32px;
  width: 32px;
  vertical-align: middle;
  margin-left: 5px;
  background: #3384FE;
  color: #ffffff;
  font-size: 25.6px;
  text-align: center;
  line-height: 32px;
  cursor: pointer;
  display: none;
}



