input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.eboard-pagination-left {
  display: inline-block;
  width: 26px;
  height: 26px;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
}
.eboard-pagination-left.disabled {
  pointer-events: none;
  opacity: 0.3;
  cursor: not-allowed;
}
.eboard-pagination-left > .eboard-icon {
  font-size: 20.8px;
  color: #ffffff;
  line-height: 26px;
}
.eboard-pagination-right {
  display: inline-block;
  width: 26px;
  height: 26px;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
  margin-left: 10px;
}
.eboard-pagination-right.disabled {
  pointer-events: none;
  opacity: 0.3;
  cursor: not-allowed;
}
.eboard-pagination-right > .eboard-icon {
  font-size: 20.8px;
  color: #ffffff;
  line-height: 26px;
}
.eboard-pagination-bottom {
  position: fixed;
  height: 44px;
  bottom: 54px;
  left: 50%;
  margin-left: -40px;
  font-size: 18px;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 999px;
  padding-top: 4px;
  padding-left: 10px;
}
.eboard-pagination-current {
  width: 40px !important;
  height: 26px !important;
  border: 1px solid transparent;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  background: #dddddd;
  text-align: center;
  vertical-align: middle;
  border-radius: 5px;
  margin-right: 10px;
}
.eboard-pagination-current:hover {
  border: 1px solid #09ca51;
}
.eboard-pagination-total {
  padding: 0.2rem;
  color: #ffffff;
  display: inline-block;
  vertical-align: middle;
}
.eboard-pagination-bottomGo {
  display: inline-block;
  margin: 0 5px 0 0;
  background-color: #666666;
  color: #ffffff;
  border-radius: 100%;
  text-align: center;
  width: 35px;
  height: 35px;
  line-height: 35px;
  cursor: pointer;
}
.eboard-pager-enter-from-right {
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  animation: page-enter-from-right ease 500ms;
}
.eboard-pager-enter-from-left {
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  animation: page-enter-from-left ease 500ms;
}
.eboard-pager-leave-to-left {
  -webkit-transform: translate(-150%, -50%);
  -moz-transform: translate(-150%, -50%);
  -ms-transform: translate(-150%, -50%);
  -o-transform: translate(-150%, -50%);
  transform: translate(-150%, -50%);
  animation: page-leave-to-left ease 500ms;
}
.eboard-pager-leave-to-right {
  -webkit-transform: translate(50%, -50%);
  -moz-transform: translate(50%, -50%);
  -ms-transform: translate(50%, -50%);
  -o-transform: translate(50%, -50%);
  transform: translate(50%, -50%);
  animation: page-leave-to-right ease 500ms;
}
.eboard-disable .eboard-pagination-left,
.eboard-disable .eboard-pagination-right {
  display: none;
}
.eboard-disable .eboard-pagination-bottom {
  pointer-events: none;
}
.eboard-disable .eboard-pagination-current {
  pointer-events: none;
  background-color: transparent!important;
}
.eboard-disable .eboard-pagination-bottomGo {
  display: none;
}
@keyframes page-enter-from-right {
  from {
    -webkit-transform: translate(50%, -50%);
    -moz-transform: translate(50%, -50%);
    -ms-transform: translate(50%, -50%);
    -o-transform: translate(50%, -50%);
    transform: translate(50%, -50%);
  }
  to {
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
}
@keyframes page-enter-from-left {
  from {
    -webkit-transform: translate(-150%, -50%);
    -moz-transform: translate(-150%, -50%);
    -ms-transform: translate(-150%, -50%);
    -o-transform: translate(-150%, -50%);
    transform: translate(-150%, -50%);
  }
  to {
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
}
@keyframes page-leave-to-left {
  from {
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
  to {
    -webkit-transform: translate(-150%, -50%);
    -moz-transform: translate(-150%, -50%);
    -ms-transform: translate(-150%, -50%);
    -o-transform: translate(-150%, -50%);
    transform: translate(-150%, -50%);
  }
}
@keyframes page-leave-to-right {
  from {
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
  to {
    -webkit-transform: translate(50%, -50%);
    -moz-transform: translate(50%, -50%);
    -ms-transform: translate(50%, -50%);
    -o-transform: translate(50%, -50%);
    transform: translate(50%, -50%);
  }
}



